// @ts-ignore

import react from "react"
import TextField from "@mui/material/TextField";
import FilePresentIcon from "@mui/icons-material/FilePresent";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import InputLabel from "@mui/material/InputLabel";
import TaskIcon from "@mui/icons-material/Task";
import axios from "axios"
import CircularProgress from "@mui/material/CircularProgress";
import Box from "@mui/material/Box";
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import SyncProblemIcon from "@mui/icons-material/SyncProblem";
import { Link } from "react-router-dom";
import path from "./../../../const/path"

const RegisterForm = ()=>{

    const [currentStep, setCurrentStep] = react.useState(0)
    const [btnTitle, setBtnTitle] = react.useState("Suivant")
    const [send, setSend] = react.useState(1)
    const [loading, setLoad] = react.useState(false)
    const [end, setEnd] = react.useState(false)
    const [success, setSuccess] = react.useState(false)
    const [err, setErr] = react.useState(false)

    const step: any = [
      {
        title: "Informations personnelles",
        isActive: false,
      },
      {
        title: "Vous'y êtes presque",
        isActive: false,
      },
      {
        title: "Importer votre CV",
        isActive: false,
      }
    ];


    const [secondIcon, setSecondIcon] = react.useState(
      <TaskIcon className="cv-file" />
    );
    
    const [icon, setIcon] = react.useState(
      <FilePresentIcon className="cv-file" />
    );
    const [cv, setCV] = react.useState("")
    const [title, setTitle] = react.useState("")
    
    const handleFile = (event:any)=>{
        setCV(event?.target?.files[0])
        setTitle(event?.target?.files[0]?.name)
        setIcon(secondIcon)
    }

        const [sex, setSex] = react.useState("homme");
        const [tel, setTel] = react.useState("");
        const [cni, setCni] = react.useState("");
        const [email, setEmail] = react.useState("")

        const handleSexChange = (event: any) => {
          setSex(event.target.value);
        };

    const handleStepChange = ()=>{
        setSend(send+1)

        if (step.length-1 > currentStep) {
          setCurrentStep(currentStep + 1);
        }
        if (step.length-2 === currentStep) {
          setBtnTitle("Envoyer ma demande");
        }
    }

    const [name, setName] = react.useState("")
    const [surname, setSurname] = react.useState("")
    const [age, setAge] = react.useState("10")

    const handleSaveSeeker = (event:any)=>{
      event.preventDefault()

      console.log(send)

      if(send === 4){
        setLoad(true)
        setEnd(true)

        const seekerData = new FormData();

        seekerData.append("file", cv);
        seekerData.append("sex", sex);
        seekerData.append("phone", tel);
        seekerData.append("cni", cni);
        seekerData.append("name", name);
        seekerData.append("surname", surname);
        seekerData.append("age", age);
        seekerData.append("email", email);

        axios
          .post(path.api_url + "/seeker", seekerData)
          .then((res) => {
            setSuccess(true);
            setSend(1);
            setLoad(false);
          })
          .catch((err) => {
            setErr(true);
            setSend(1);
            setLoad(false);
            console.log(err);
          });
      }
    }

    return (
      <form method="post" onSubmit={handleSaveSeeker} autoComplete="false">
        {end === false && (
          <div className="card home card-widget">
            <div className="card-header">
              <h1 className="white title">{step[currentStep]?.title}</h1>
              <br />
            </div>
            {currentStep === 0 && (
              <div className="card-content flexed-form">
                <TextField
                  id="outlined-basic"
                  label="Nom"
                  variant="outlined"
                  fullWidth
                  autoFocus
                  value={name}
                  onChange={(event: any) => setName(event?.target?.value)}
                />
                <TextField
                  id="outlined-basic"
                  label="Prénom"
                  variant="outlined"
                  fullWidth
                  value={surname}
                  onChange={(event: any) => setSurname(event?.target?.value)}
                />
                <TextField
                  id="outlined-basic"
                  label="Age"
                  variant="outlined"
                  type={"number"}
                  fullWidth
                  value={age}
                  onChange={(event: any) => setAge(event?.target?.value)}
                />
              </div>
            )}
            {currentStep === 1 && (
              <div className="card-content flexed-form">
                <TextField
                  id="outlined-basic"
                  label="Numéro de CNI"
                  variant="outlined"
                  fullWidth
                  autoFocus
                  value={cni}
                  onChange={(event: any) => setCni(event?.target?.value)}
                />
                <TextField
                  id="outlined-basic"
                  label="Numéro de téléphone"
                  variant="outlined"
                  fullWidth
                  value={tel}
                  onChange={(event: any) => setTel(event?.target?.value)}
                />
                <TextField
                  id="outlined-basic"
                  label="Adresse email"
                  variant="outlined"
                  fullWidth
                  value={email}
                  onChange={(event: any) => setEmail(event?.target?.value)}
                />
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">Sexe</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={sex}
                    label="Age"
                    onChange={handleSexChange}
                  >
                    <MenuItem value={"homme"}>Homme</MenuItem>
                    <MenuItem value={"femme"}>Femme</MenuItem>
                  </Select>
                </FormControl>
              </div>
            )}
            {currentStep === 2 && (
              <div className="card-content center-form column-form">
                <label htmlFor="cv">
                  <div className="cv-container">{icon}</div>
                </label>
                <input
                  type={"file"}
                  id="cv"
                  className="d-none"
                  onChange={handleFile}
                />
                {title && (
                  <div key={"1"} className="subtitle">
                    {title}
                  </div>
                )}
              </div>
            )}
            <div className="card-footer">
              <button
                className="btn btn-primary-light btn-normal mt-10"
                onClick={handleStepChange}
                type="submit"
              >
                {btnTitle}
              </button>
            </div>
          </div>
        )}
        {loading && (
          <div className="card home card-widget flex-center">
            <div className="card-header flex-center">
              <span className="white center">
                Envoi de votre demande en cour...
              </span>
            </div>
            <div className="card-content flexed-form">
              <CircularProgress />
            </div>
          </div>
        )}
        {success && (
          <div className="card home card-widget flex-center">
            <div className="card-header flex-center">
              <span className="white center">
                Votre demande à été prise en compte, nous vous recontacterons
                dès que possible !
              </span>
            </div>
            <div className="card-content flexed-center">
              <br />
              <Box sx={{ display: "flex" }} className="guihon-icon">
                <CheckCircleOutlineIcon />
              </Box>
              <br />
              <Box sx={{ display: "flex" }} className="">
                <Link to="/" className="btn btn-primary-light btn-normal mt-10">
                  Acceuil
                </Link>
              </Box>
            </div>
          </div>
        )}

        {err && (
          <div className="card home card-widget flex-center">
            <div className="card-header flex-center">
              <span className="white center">
                Une erreur est survenue lors de la soumission de votre demande !
              </span>
            </div>
            <div className="card-content flexed-center">
              <br />
              <Box sx={{ display: "flex" }} className="guihon-icon-error">
                <SyncProblemIcon />
              </Box>
              <br />
              <Box sx={{ display: "flex" }} className="">
                <Link to="/" className="btn btn-primary-light btn-normal mt-10">
                  Reéssayer
                </Link>
              </Box>
            </div>
          </div>
        )}
      </form>
    );
}

export default RegisterForm
